import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const mapOption = {
    namespaced: true,
    action: {},
    mutations: {
        ADDDRAWSELECTFEATUREID(state, idList) {
            state.drawSelectFeaturesId = idList;
        }
    },
    state: {
        drawSelectFeaturesId: [],
    }
}

//监测设备模块
const deviceOption = {
    namespaced: true,
    action: {},
    mutations: {
        //添加设备列表
        ADDDEVICE(state, devicelist) {
            state.deviceList = devicelist;
        },
        //添加监测点位
        ADDPOINT(state, pointlist) {
            state.pointList = pointlist;
        }
    },
    state: {
        deviceList: [], //所有监测设备列表
        pointList: [],   //所有监测点位列表
    }
}


//降雨量模块
const rainfallOption = {
    namespaced: true,
    action: {},
    mutations: {
        //添加降雨量监测设备信息列表
        ADDDEVICE(state, devicelist) {
            state.deviceList = devicelist;
        },
    },
    state: {
        deviceList: [], //降雨量监测设备信息列表
    }
}
//地质生态监测
const ecogeologyOption = {
    namespaced: true,
    actions: {},
    mutations:{
        //添加地貌形态监测设备信息列表    
        ADDRORMDEVICE(state, devicelist) {
            state.deviceList_lform = devicelist;
        },
        //添加土地利用监测设备信息列表
        ADDUSEDEVICE(state, devicelist) {
            state.deviceList_luse = devicelist;
        },
        //添加植被覆盖监测设备信息列表
        ADDTCOVERDEVICE(state, devicelist) {
            state.deviceList_tcover = devicelist;
        },
        //添加湿地变化监测设备信息列表
        ADDWDISTRIBUTEDEVICE(state, devicelist) {
            state.deviceList_wdistribute = devicelist;
        },
        //添加水体分布监测设备信息列表
        ADDWETCHANGEDEVICE(state, devicelist) {
            state.deviceList_wetchange = devicelist;
        },
    },
    state: {
        deviceList_lform:[],
        deviceList_luse:[],
        deviceList_tcover:[],
        deviceList_wdistribute:[],
        deviceList_wetchange:[],
    }
}
//地质环境监测
const envgeologyOption = {
    namespaced: true,
    actions: {},
    mutations:{
        //添加滑坡监测设备信息列表    
        ADDSLIDDEVICE(state, devicelist) {
            state.deviceList_slid = devicelist;
        },
        //添加崩塌监测设备信息列表
        ADDCOLLAPSDEVICE(state, devicelist) {
            state.deviceList_collaps = devicelist;
        },
        //添加泥石流监测设备信息列表
        ADDFLOWDEVICE(state, devicelist) {
            state.deviceList_debriflow = devicelist;
        },
        //添加地面塌陷监测设备信息列表
        ADDGCOLLAPSDEVICE(state, devicelist) {
            state.deviceList_groundcollaps = devicelist;
        },
        //添加地面沉降监测设备信息列表
        ADDGSUBDEVICE(state, devicelist) {
            state.deviceList_groundsub = devicelist;
        },
        //添加地裂缝监测设备信息列表
        ADDGCRACKDEVICE(state, devicelist) {
            state.deviceList_groundcrack = devicelist;
        },
        //添加活动断裂监测设备信息列表
        ADDACRACKDEVICE(state, devicelist) {
            state.deviceList_activecrack = devicelist;
        },

        //添加地下水环境监测设备信息列表
        ADDUNDERWENVDEVICE(state, devicelist) {
            state.deviceList_underwaterenv = devicelist;
        },
        //添加土地质量监测设备信息列表
        ADDGQUALITYDEVICE(state, devicelist) {
            state.deviceList_groundquality = devicelist;
        },
        //添加城市垃圾监测设备信息列表
        ADDURBANGBDEVICE(state, devicelist) {
            state.deviceList_urbangarbage = devicelist;
        },
    },
    state: {
        deviceList_slid:[],
        deviceList_collaps:[],
        deviceList_debriflow:[],
        deviceList_groundcollaps:[],
        deviceList_groundsub:[],
        deviceList_groundcrack:[],
        deviceList_activecrack:[],
        deviceList_underwaterenv:[],
        deviceList_groundquality:[],
        deviceList_urbangarbage:[],
    }
}
//土壤环境模块
const soilConditionOption = {
    namespaced: true,
    action: {},
    mutations: {
        //添加土壤化验监测设备信息列表
        ADDSOILTESTDEVICE(state, devicelist) {
            state.deviceList_test = devicelist;
        },
        //添加土壤采样监测设备信息列表
        ADDSOILSAMPDEVICE(state, devicelist) {
            state.deviceList_samp = devicelist;
        },
    },
    state: {
        deviceList_test: [], //土壤化验监测设备信息列表
        deviceList_samp: [], //土壤采样监测设备信息列表
    }
}
//地表水模块
const surfaceWaterOption = {
    namespaced: true,
    action: {},
    mutations: {
        //添加地表水水位监测设备信息列表
        ADDLEVELDEVICE(state, devicelist) {
            state.deviceList_level = devicelist;
        },
        //添加地表水水质监测设备信息列表
        ADDQUALITYDEVICE(state, devicelist) {
            state.deviceList_quality = devicelist;
        },
    },
    state: {
        deviceList_level: [], //地表水水位监测设备信息列表
        deviceList_quality: [], //地表水水质监测设备信息列表
    }
}

//地下水模块
const groundWaterOption = {
    namespaced: true,
    action: {},
    mutations: {
        //添加地下水监测设备信息列表
        ADDLEVELDEVICE(state, devicelist) {
            state.deviceList_level = devicelist;
        },
        //添加地下水水质监测设备信息列表
        ADDQUALITYDEVICE(state, devicelist) {
            state.deviceList_quality = devicelist;
        },
    },
    state: {
        deviceList_level: [], //地下水水位监测设备信息列表
        deviceList_quality: [], //地下水水质监测设备信息列表
    }
}

//土壤重金属模块
const soilHeavyMetalOption = {
    namespaced: true,
    action: {},
    mutations: {
        //添加土壤重金属设备列表
        ADDDEVICE(state, devicelist) {
            state.deviceList = devicelist;
        }
    },
    state: {
        deviceList: [],
    }
}

//洪水淹没模块
const submergeOption = {
    namespaced: true,
    actions: {},
    mutations: {
        HASPROFILE(state, flag) {
            state.hasProfile = flag;
        },
        CHANGEDRAWSTATUE(state, value) {
            state.drawStatue = value;
        },
        CHANGEPROFILELOCATION(state, value) {
            state.isProfileLocation = value;
        },
        CHANGEPROFILESTATE(state, value) {
            state.profileState = value;
        }
    },
    state: {
        drawStatue: 0, //0表示还没有画，1表示画洪水淹没，2表示画剖面分析
        isProfileLocation: true, //是否正在剖面线上漫游
        profileState: 0, //绘制剖面线的状态，0表示未绘制，1表示正在绘制，2表示剖面线计算完成
    }
}

//二三维地质一张图
const geologicalmapOption = {
    namespaced: true,
    actions: {},
    mutations: {
        CLEARALLWMS(state) {
            for (var i = 0; i < state.wmsArray.length; i++) {
                state.wmsArray[i].show = false;
            }
        }
    },
    state: {
        wmsArray: [ //加载geoserver发布的地图
            { layerName: 'K4936D01B', name: 'K4936D01B', typeName: 'monitorSystem:K4936D01B.WP', show: false },
            { layerName: 'K4936D01D', name: 'K4936D01D', typeName: 'monitorSystem:K4936D01D.WP', show: false },
            { layerName: 'K4936D01J', name: 'K4936D01J', typeName: 'monitorSystem:K4936D01J.WL', show: false },
            { layerName: 'K4936D02H', name: 'K4936D02H', typeName: 'monitorSystem:K4936D02H.WP', show: false },
            { layerName: 'K4936D04N', name: 'K4936D04N', typeName: 'monitorSystem:K4936D04N.WP', show: false },
            { layerName: 'K4936D05M', name: 'K4936D05M', typeName: 'monitorSystem:K4936D05M.WP', show: false },
            { layerName: 'K4936D06S', name: 'K4936D06S', typeName: 'monitorSystem:K4936D06S.WP', show: false },
            { layerName: 'K4936D07B', name: 'K4936D07B', typeName: 'monitorSystem:K4936D07B.WP', show: false },
            { layerName: 'K4936D07H', name: 'K4936D07H', typeName: 'monitorSystem:K4936D07H.WP', show: false },
            { layerName: 'K4936D08D', name: 'K4936D08D', typeName: 'monitorSystem:K4936D08D.WL', show: false },
            { layerName: 'K4936D09G', name: 'K4936D09G', typeName: 'monitorSystem:K4936D09G.WP', show: false },
            { layerName: 'K4936D11K', name: 'K4936D11K', typeName: 'monitorSystem:K4936D11K.WT', show: false },
            { layerName: 'K4936D12C', name: 'K4936D12C', typeName: 'monitorSystem:K4936D12C.WT', show: false },
            { layerName: 'K4936D13P', name: 'K4936D13P', typeName: 'monitorSystem:K4936D13P.WL', show: false },
            { layerName: 'K4936D13S', name: 'K4936D13S', typeName: 'monitorSystem:K4936D13S.WT', show: false },
            { layerName: 'K4936L01J', name: 'K4936L01J', typeName: 'monitorSystem:K4936L01J.WL', show: false },
        ],
    }
}


//用户操作模块
const userOption = {
    namespaced: true,
    actions: {},
    mutations: {
        CHANGECOLLAPSE(state, flag) {    //修改是否折叠
            state.collapse = flag;
        },
        ADDUSER(state, userName, password) {
            state.ms_userName = userName;
            state.ms_password = password;
        }
    },
    state: {
        collapse: false,    //是否折叠
        ms_userName: '',
        ms_password: ''
    }
}


//创建store 命名空间
export default new Vuex.Store({
    modules: {
        mapAbout: mapOption,    //地图模块
        deviceAbout: deviceOption, //监测设备模块
        rainfallAbout: rainfallOption, //降雨量模块
        surfaceWaterAbout: surfaceWaterOption, //地表水模块
        groundWaterAbout: groundWaterOption, //地下水模块
        soilHeavyMetalAbout: soilHeavyMetalOption, //土壤重金属模块
        submergeAbout: submergeOption, //洪水淹没模块
        geologicalmapAbout: geologicalmapOption, //二三维地质一张图模块
        ecogeologyAbout: ecogeologyOption, //地质生态监测
        envgeologyAbout: envgeologyOption, //地质环境监测
        soilConditionAbout: soilConditionOption, //土壤信息
        userAbout: userOption, //用户模块
    }
})